.word-defenition-match-wrap {
    max-width: 640px;
    margin: auto;

    > .defenition-block {
        border: 1px solid var(--color-grey);
        padding: 20px 50px;
        min-height: 100px;
        text-align: center;

        &.-correct {
            border-color: var(--color-green);
        }

        &.-wrong {
            border-color: var(--color-red);
        }

        > .img-holder {
            width: 300px;
            height: 200px;
            margin: 0 auto var(--spacing-half);

            > img {
                object-fit: contain;
                object-position: center;
                height: 100%;
            }
        }
    }
}

.options-block {
    > .subhead {
        padding: 10px;
        text-align: center;
        color: var(--color-grey);

        &.-correct {
            color: var(--color-green);
        }

        &.-wrong {
            color: var(--color-red);
        }
    }

    > .options-list {
        > .option-item {
            cursor: pointer;
            margin: 10px auto;
            width: 100%;
            max-width: 320px;
            padding: 10px 20px;
            border: 1px solid var(--border-color);
            background-color: var(--color-grey-pale);
            text-align: center;
            border-radius: var(--standard-border-radius);

            &:hover {
                background-color: var(--color-light-green);
                border-color: var(--color-light-green);
                color: var(--color-blue);
            }

            &.-correct {
                background-color: var(--color-green-pale);
                color: var(--color-green);
            }

            &.-wrong {
                background-color: var(--color-red-pale);
                color: var(--color-red);
            }
        }
    }
}

.success-block {
    align-items: center;
    background-color: #fff;
    border: 1px solid #e6e9ee;
    border-radius: 8px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 784px;
    min-height: 646px;
    padding: 42px 16px;
    width: 100%;

    > h2 {
        margin-bottom: var(--spacing-full);
    }

    > .recap-list {
        > .recap-list-item {
            margin-bottom: var(--spacing-half);
        }
    }
}

.interesting-fact {
    padding: 2% 3%;
    margin-top: var(--spacing-half);
    margin-bottom: var(--spacing-half);
    border: 1px dashed var(--border-color);
    background-color: var(--color-grey-pale);

    > h3 {
        margin-bottom: var(--spacing-half);
        text-align: center;
        font-variant: small-caps;
    }

    > .description {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;

        > .image-holder {
            width: 50%;
            max-width: 300px;
        }

        > p {
            width: 100%;
        }
    }
}